import React from 'react';
import { connect } from 'react-redux';
import { Navigate } from 'react-router-dom';
import { Field } from 'redux-form';
import { reduxForm } from 'redux-form';
import { login } from '../../redux/auth-reducer';
import { reqired } from '../../utils/validators/validators';
import { CreateField, Input } from '../common/FormsControls/FormsControls';
import style from '../common/FormsControls/FormsControls.module.css'

const LoginForm = ({handleSubmit, error, captchaUrl}) => {
    return (
        <form onSubmit={handleSubmit}>
           
                { CreateField('Email', 'email', Input, [reqired]) }
                { CreateField('Password', 'password', Input, [reqired], {type: 'password'} ) }
                { CreateField(null, 'rememberMe', Input, [], {type: 'checkbox'}, 'remember me' ) }

                {/* <Field placeholder={'Email'} name={'email'} component={ Input } validate={[ reqired ]} />
                <Field placeholder={'Password'} name={'password'} component={Input} validate={[ reqired ]} type={'password'} /> 
                <Field component={Input} name={'rememberMe'} type={'checkbox'} /> remember me */}
            
            {captchaUrl && <img src={captchaUrl} />}
            {captchaUrl && CreateField('Symbols from image', 'captcha', Input, [reqired], {} ) }

            { error && <div className={ style.formSummaryError }>
                {error}
            </div>}
            <div>
                <button>Login</button>
            </div>
        </form>
    )
}

const LoginReduxForm = reduxForm({
    //уникальное имя для формы
    form: 'login'
})(LoginForm)

const Login = (props) => {
    const onSubmit = (formData) => {
        props.login(formData.email, formData.password, formData.rememberMe, formData.captcha);
    }

    if (props.isAuth) {
        return <Navigate to='/profile' />
    }
    
    return <div>
        <h1>LOGIN</h1>
        <LoginReduxForm onSubmit={onSubmit} captchaUrl={props.captchaUrl} />
    </div>
}

const mapStateToProps = (state) => ({
    captchaUrl: state.auth.captchaUrl,
    isAuth: state.auth.isAuth
})

export default connect(mapStateToProps, { login }) (Login);